.users-card {
  width: 280px;
  margin-bottom: 20px;
  background-color: #f2f2f2;

  @media (min-width: @tablet-width) {
    width: 300px;
    margin-bottom: 50px;

    &:nth-child(2n+1) {
      margin-right: 3%;
    }
  }

  @media (min-width: @desktop-width) {
    &:not(:nth-child(3n+3)) {
      margin-right: 20px;
    }

    &:nth-child(3n+3) {
      margin-right: 0;
    }
  }
}

.users-card__photo {
  width: 280px;
  height: 180px;

  @media (min-width: @tablet-width) {
    width: 300px;
    height: 300px;
  }

  @media (min-width: @desktop-width) {
    height: 400px;
  }
}

.users-card__info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  min-height: 121px;
  padding-left: 15px;

  @media (min-width: @tablet-width) {
    min-height: 155px;
  }
}

.users-card__author {
  display: flex;
}

.users-card__time {
  display: inline-block;
  text-align: left;
  font-family: @open-sans;
  font-size: 12px;
  font-weight: 300;
  text-transform: uppercase;
  margin: 0;
}

.users-card__name {
  font-size: 14px;
  text-transform: none;
  font-weight: 700;
  margin: 0;
  margin-right: 5px;
}

.users-card__review {
  display: inline-block;
  text-align: left;
  color: #283645;
  font-family: @open-sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  quotes: none;
  margin-bottom: 10px;
}

.users-card__likes {
  display: flex;
  margin-bottom: 20px;
}

.users-card__icon {
  margin-top: 5px;
  margin-right: 10px;
}

.users-card__sign {
  margin: 0;
  color: #d22856;
  font-family: @open-sans;
  font-size: 12px;
  font-weight: 400;
  line-height: 24px;
  text-transform: uppercase;
}
